import './scss/styles.scss';
import { WebLarekAPI } from './components/WebLarekAPI';
import { API_URL, CDN_URL } from "./utils/constants";
import { EventEmitter } from "./components/base/events";
import { cloneTemplate, ensureElement } from './utils/utils';
import { AppData } from "./components/base/model";
import { Page } from './components/page';
import { Card, CardPreview, BasketCard } from './components/card';
import { Modal } from './components/modal';
import { Basket } from './components/common/basket';
import { Order } from './components/common/order';
const cardTemplate = ensureElement('#card-catalog');
const cardPreview = ensureElement('#card-preview');
const basketTemplate = ensureElement('#basket');
const basketItem = ensureElement('#card-basket');
const orderTemplate = ensureElement('#order');
const events = new EventEmitter();
const api = new WebLarekAPI(CDN_URL, API_URL);
const appData = new AppData({}, events);
const page = new Page(document.body, events);
const modal = new Modal(ensureElement('#modal-container'), events);
const basket = new Basket(cloneTemplate(basketTemplate), events);
const order = new Order(cloneTemplate(orderTemplate), events);
events.onAll(({ eventName, data }) => {
    console.log(eventName, data);
});
events.on('items:changed', () => {
    page.catalog = appData.catalog.map((item) => {
        const card = new Card(cloneTemplate(cardTemplate), {
            onClick: () => {
                events.emit('card:select', item);
            },
        });
        return card.render({
            title: item.title,
            image: item.image,
            category: item.category,
            price: item.price,
        });
    });
});
events.on('preview:changed', (item) => {
    const showItem = (item) => {
        const card = new CardPreview(cloneTemplate(cardPreview), {
            onClick: () => {
                events.emit('basket:add', item);
                modal.close();
            },
        });
        modal.render({
            content: card.render({
                title: item.title,
                image: item.image,
                category: item.category,
                price: item.price,
                description: item.description,
            }),
        });
        if (item.price === null || appData.checkItemToBasked(item)) {
            card.blockButton();
        }
    };
    if (item) {
        api
            .getItem(item.id)
            .then((result) => {
            item.description = result.description;
            showItem(item);
        })
            .catch((err) => {
            console.log(err);
        });
    }
    else {
        modal.close();
    }
});
events.on('card:select', (item) => {
    appData.setPreview(item);
});
events.on('modal:open', () => {
    page.locked = true;
});
events.on('modal:close', () => {
    page.locked = false;
    appData.addPayMethod(null);
    appData.addAddress('');
    appData.setContactInfoForm('email', '');
    appData.setContactInfoForm('phone', '');
});
events.on('basket:open', () => {
    modal.render({
        content: basket.render(),
    });
});
events.on('basket:add', (item) => {
    appData.addToBasket(item);
});
events.on('basket:update', () => {
    const basketItems = appData.basket;
    basket.items = basketItems.map((item, index) => {
        const basketCard = new BasketCard(cloneTemplate(basketItem), {
            onClick: () => {
                events.emit('item:delete', item);
            },
        });
        basketCard.basketItemIndex = index + 1;
        return basketCard.render({
            title: item.title,
            price: item.price,
        });
    });
    basket.totalPrice = appData.getFullPrice();
    page.counter = basketItems.length;
});
events.on('item:delete', (item) => {
    appData.removeFromBasket(item);
});
events.on('order:open', () => {
    modal.render({
        content: order.render({
            payment: null,
            address: '',
            valid: false,
            errors: [],
        }),
    });
    order.toggleClass('');
});
events.on('paymentMethod:change', (method) => {
    console.log('paymentMethod:change');
    appData.addPayMethod(method.paymentMethod);
});
events.on('order.address:change', (email) => {
    console.log('order.address:change');
    appData.addAddress(email.value);
});
events.on('orderformErrors:change', (errors) => {
    const { payment, address } = errors;
    order.valid = !payment && !address;
    order.errors = Object.values({ payment, address })
        .filter((i) => !!i)
        .join('; ');
});
api.getItemList()
    .then(appData.setCatalog.bind(appData))
    .catch((err) => {
    console.log(err);
});
