import { Component } from "../base/component";
import { createElement, formatNumber } from "../../utils/utils";
export class Basket extends Component {
    constructor(container, events) {
        super(container);
        this.events = events;
        this._list = this.container.querySelector('.basket__list');
        this._totalPrice = this.container.querySelector('.basket__price');
        this._button = this.container.querySelector('.basket__button');
        if (this._button) {
            this._button.addEventListener('click', () => {
                events.emit('order:open');
            });
        }
        this.items = [];
    }
    set items(items) {
        if (items.length) {
            this._list.replaceChildren(...items);
            this.setDisabled(this._button, false);
        }
        else {
            this._list.replaceChildren(createElement('p', {
                textContent: 'Корзина пуста'
            }));
            this.setDisabled(this._button, true);
        }
    }
    set totalPrice(totalPrice) {
        this.setText(this._totalPrice, formatNumber(totalPrice) + ' синапсов');
    }
}
