export const isModel = (obj) => {
    return obj instanceof Model;
};
export class Model {
    constructor(data, events) {
        this.events = events;
        Object.assign(this, data);
    }
    emitChanges(event, payload) {
        this.events.emit(event, payload !== null && payload !== void 0 ? payload : {});
    }
}
//представляет собой конкретную модель приложения. Управляет состоянием галереи, корзины и заказа, 
//а также выполняет различные операции с данными.
export class AppData extends Model {
    constructor() {
        super(...arguments);
        this.basket = [];
        this.order = {
            payment: null,
            address: '',
            email: '',
            phone: '',
            items: [],
            total: 0,
        };
        this.formErrors = {};
    }
    //метод добавления товара в корзину    
    addToBasket(item) {
        this.basket.push(item);
        this.events.emit('basket:update');
    }
    ;
    //метод удаления товара из корзины.
    removeFromBasket(item) {
        const itemID = item.id;
        this.basket = this.basket.filter((item) => item.id !== itemID);
        this.events.emit('basket:update');
    }
    ;
    //метод полной очистки корзины.
    clearBasket() {
        this.basket = [];
        this.emitChanges('basket:update');
        this.order = {
            payment: null,
            address: '',
            email: '',
            phone: '',
            items: [],
            total: 0,
        };
    }
    ;
    //метод получения каталога товаров.
    setCatalog(items) {
        this.catalog = items;
        this.emitChanges('items:changed', { catalog: this.catalog });
    }
    ;
    //метод получения одной карточки товара по id
    setPreview(item) {
        this.preview = item.id;
        this.emitChanges('preview:changed', item);
    }
    ;
    // //метод проверки нахождения товара в корзине
    checkItemToBasked(item) {
        return this.basket.includes(item);
    }
    ;
    //метод получения общей стоимости товара в корзине.
    getFullPrice() {
        return this.basket.reduce((index, item) => {
            return index + Number(item.price);
        }, 0);
    }
    ;
    //метод получения суммы для заказа
    setPriceOrder() {
        this.order.total = this.getFullPrice();
    }
    setItemOrder() {
        this.order.items = this.basket.map((item) => item.id);
    }
    //метод выбора способа оплаты
    addPayMethod(method) {
        this.order.payment = method;
        this.validateOrder();
    }
    ;
    //метод добавления адреса.
    addAddress(address) {
        this.order.address = address;
        this.validateOrder();
    }
    ;
    //метод валидации формы заказа.
    validateOrder() {
        const errors = {};
        if (!this.order.payment) {
            errors.payment = 'Необходимо указать способ оплаты';
        }
        if (!this.order.address) {
            errors.address = 'Необходимо указать адрес доставки';
        }
        this.formErrors = errors;
        this.events.emit('orderformErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    ;
    //метод ввода Email и номера телефона
    setContactInfoForm(field, value) {
        this.order[field] = value;
        this.validateContactInfoForm();
    }
    ;
    //валидация контактных данных
    validateContactInfoForm() {
        const errors = {};
        if (!this.order.email) {
            errors.email = 'Необходимо указать email';
        }
        if (!this.order.phone) {
            errors.phone = 'Необходимо указать номер телефон';
        }
        this.formErrors = errors;
        this.events.emit('contactInfoFormErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    ;
}
