import { Form } from './form';
import { ensureAllElements } from '../../utils/utils';
export class Order extends Form {
    constructor(container, events) {
        super(container, events);
        this._paymentBtns = ensureAllElements('.button_alt', container);
        this._paymentBtns.forEach((btn) => {
            btn.addEventListener('click', (evt) => {
                const button = evt.target;
                this.toggleClass(button.name);
                this.events.emit('paymentMethod:change', { paymentMethod: button.name });
            });
        });
    }
    toggleClass(name) {
        this._paymentBtns.forEach((btn) => {
            if (btn.name == name) {
                btn.classList.add('button_alt-active');
            }
            else {
                btn.classList.remove('button_alt-active');
            }
        });
    }
    set address(value) {
        this.container.elements.namedItem('address').value = value;
    }
}
